.header-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: calc(2vw + -1rem);

    ul {
        @media only screen and (max-width: 705px) {
            flex-wrap: wrap;
            margin-bottom: 1rem;
        }

        width: 70vw;
        background-color: #fff;

        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 1.5rem 2rem;
        border-radius: 5px;

        margin: 1.3rem 0;
        position: relative;

        box-shadow: 0px 4px 6px 4px rgba(91, 164, 164, 0.2);

        margin-bottom: -2rem;

        position: relative;

        li {
            list-style: none;

            background-color: var(--background-color);
            color: var(--primary-color);
            font-weight: 700;
            padding: 0 0 0 0.5rem;
            display: flex;
            align-items: center;
            border-radius: 5px;

            @media only screen and (max-width: 705px) {
                margin-bottom: 0.5rem;
            }

            &:not(:last-child) {
                margin-right: 1rem;
            }

            .close {
                background-color: var(--primary-color);
                border: none;
                border-radius: 0 5px 5px 0;
                transition: all 0.2s;
                margin-left: 0.5rem;
                img {
                    padding: 0.5rem;
                    cursor: pointer;
                }

                &:hover {
                    background-color: var(--very-dark-greyish);
                }
            }

        }

        a {
            color: var(--very-dark-greyish);
            position: absolute;
            right: 1.5rem;
            text-decoration: none;

            &:hover {
                color: var(--primary-color);
                text-decoration: underline;
            }
        }
    }
}
